.Button {
  padding:1rem;
  border-radius: 5px;
  margin: 2rem 0;
  font-size: 1.6rem;
  color:white;
  cursor:pointer;
}

.Primary {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#54eb61), color-stop(60%, rgb(53, 231, 47)), to(#24e51d));
  background-image: -webkit-linear-gradient(#54eb61, rgb(53, 231, 47) 60%, #24e51d);
  background-image: -o-linear-gradient(#54eb61, rgb(53, 231, 47) 60%, #24e51d);
  background-image: linear-gradient(#54eb61, rgb(53, 231, 47) 60%, #24e51d);
  background-repeat: no-repeat;
  background-color: #24e51d;
  border: 1px solid #4fcf4b;
  color:white;
}

.Primary:hover {
  border: 1px solid #4ab147;
}

.Secondary {
  background-image: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(60%, #e9ecef), to(#dde2e6));
  background-image: -webkit-linear-gradient(white, #e9ecef 60%, #dde2e6);
  background-image: -o-linear-gradient(white, #e9ecef 60%, #dde2e6);
  background-image: linear-gradient(white, #e9ecef 60%, #dde2e6);
  border: 1px solid white;
  background-repeat: no-repeat;
  color: #495057;
  margin-right: 1rem;
}

.Secondary:hover {
  border: 1px solid #cbcfcb;
}



